var React = require('react/addons');
var Debug = require("../utils/debug");

var CheckNode=React.createClass({
    displayName:'Common Button',
    getDefaultProps: function(){
        return{
            id : "checkbox default id",
            value : "checkbox default value",
            type : "checkbox",
            name: "checkbox default name"
        }
    },
    getInitialState:function(){
        return {
            dis:false,
            cssClass:'primary'
        };
    },
    _doClick:function(e){ //e is short for event
      e.preventDefault()
      if(this.props.doAction){
          this.props.doAction();
      }
    },
    render:function(){
        return (<span> <input type={this.props.type} id={this.props.id} value={this.props.value} /> {this.props.name}
            </span>);
        }
});
module.exports=CheckNode;



