var React = require('react/addons');/** * 使用方式 *  <PageSizeSelect onselect={this._selectPageSize} /> *  其中onselect事件是回调分页所在parent中执行分页查询的方法，该方法会把所选的当前的分页大小传过去 */var PageSizeSelect=React.createClass({    _selectPageSize:function(e){        if(e.target&& e.target.value>0){            this.props.onselect(e.target.value);        }    },    _setPageNum:function(e){        if(e.keyCode === 13){            e.preventDefault();            if(e.target.value>0){                this.props._setPageNum(e.target.value);            }        }    },    render:function(){      var select=<b className="page-size-left">到第<input type="text" className="form-control input-xs" onKeyUp={this._setPageNum}/>页<select className="form-control input-nm" onChange={this._selectPageSize}><option value="10">每页显示10条</option><option value="15">每页显示15条</option><option value="20">每页显示20条</option><option value="30">每页显示30条</option><option value="40">每页显示40条</option><option value="50">每页显示50条</option><option value="100">每页显示100条</option></select>&nbsp;</b>;      return (select);    }});module.exports = PageSizeSelect;


